/*Colors*/
/*Viewports*/
body {
  margin: 0 auto;
  background-color: white;
  color: black;
  padding: 0;
  font-family: "Lato", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 18px;
}
@media (min-width: 1200px) {
  body {
    display: flex;
    flex-direction: column;
    min-height: 100%;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-display: swap;
}
.additional-product-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.additional-products__title {
  color: black;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
  position: relative;
  margin: 0;
  padding: 0 0 36px 0;
  box-shadow: -20px 0 0 0 white, 21px 0 0 0 white;
  background-color: white;
  width: fit-content;
}
@media (min-width: 768px) {
  .additional-products__title {
    padding: 0 0 36px 47px;
    box-shadow: -20px 0 0 0 white, 36px 0 0 0 white;
  }
}
