    padding: 0 0 81px 0;
    box-shadow: -40px 0 0 0 white, 35px 0 0 0 white;
    line-height: 36px;
    font-size: 36px;
    width: 348px;
  }
}

.additional-products__title::before {
  content: "";
  position: absolute;
  right: 0;
  top: 14px;
  width: 100vw;
  height: 2px;
  background: #68b738;
  box-shadow: 100vw 0 0 #68b738;
  z-index: -2;
}
@media (min-width: 768px) {
  .additional-products__title::before {
    top: 14px;
  }
}
@media (min-width: 1200px) {
  .additional-products__title::before {
    top: 24px;
  }
}

.additional-products::before {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  width: 100vw;
  height: 1px;
  background: #ebebeb;
  box-shadow: 100vw 0 0 #ebebeb;
  z-index: 3;
}
@media (min-width: 768px) {
  .additional-products::before {
    display: none;
  }
}

.additional-products {
  position: relative;
  padding-top: 25px;
}
@media (min-width: 768px) {
