    padding-top: 25px;
  }
}
@media (min-width: 1200px) {
  .additional-products {
    padding-top: 75px;
  }
}

.button {
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  text-decoration: none;
  line-height: 18px;
  color: white;
  background-color: #68b738;
  text-align: center;
}
@media (min-width: 768px) {
  .button {
    line-height: 20px;
    font-size: 16px;
  }
}

.button--show-all {
  background-color: #f2f2f2;
  color: black;
}

.card {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 13px;
  position: relative;
}
@media (min-width: 768px) {
  .card {
    padding: 0 36px 39px 36px;
  }
}

@media (min-width: 768px) {
  .card:not(.card--additional):not(.card--show-all) {
    display: block;
    background: linear-gradient(white 73px, #f2f2f2 73px, #f2f2f2 430px);
  }
}

.card--show-all {
  display: grid;
}
@media (min-width: 768px) {
  .card--show-all {
    background-color: white;
    border: 2px solid #f2f2f2;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    height: 429px;
    align-self: end;
    row-gap: 0;
  }
}
@media (min-width: 768px) {
  .card--show-all {
    height: 431px;
  }
}

.card:not(.card--additional)::before {
  content: "";
  position: absolute;
  right: 0;
  width: 100vw;
  height: 1px;
  background: #ebebeb;
  box-shadow: 100vw 0 0 #ebebeb;
  z-index: -2;
}
@media (min-width: 768px) {
  .card:not(.card--additional)::before {
    display: none;
  }
}

.card__wrapper {
  padding-top: 19px;
}
@media (min-width: 768px) {
  .card__wrapper {
    margin-top: 46px;
    padding-top: 0;
    margin-bottom: 34px;
  }
}

.card--show-all .card__title {
  grid-column: 1/3;
  text-align: center;
  padding-top: 78px;
  position: relative;
}
@media (min-width: 768px) {
  .card--show-all .card__title {
    padding-top: 161px;
  }
}

.card--show-all .card__title::before {
  content: "";
  position: absolute;
  top: 45px;
  left: 132px;
  width: 30px;
  height: 2px;
  background-color: lightgray;
}
@media (min-width: 768px) {
  .card--show-all .card__title::before {
    left: 50%;
    transform: translateX(-50%);
    width: 60px;
    top: 82px;
  }
}

.card--show-all .card__title::after {
  transform: rotate(90deg);
  content: "";
  position: absolute;
  top: 45px;
  left: 132px;
  width: 30px;
  height: 2px;
  background-color: lightgray;
}
@media (min-width: 768px) {
  .card--show-all .card__title::after {
    left: 50%;
    transform: translateX(-50%) rotate(90deg);
    width: 60px;
    top: 82px;
  }
}

